import { Component, OnInit, ViewChild, HostListener } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import { UserService } from 'src/app/api/controllers/User';
import { AccountsService } from 'src/app/api/controllers/Accounts';
import { SessionService } from 'src/app/core/session/session.service';

import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';

import * as _ from 'underscore';

@Component({
  selector: 'app-expense-all-invoices',
  templateUrl: './expense-all-invoices.component.html',
  styleUrls: ['./expense-all-invoices.component.scss']
})
export class ExpenseAllInvoicesComponent implements OnInit {

  isInvoiceDataLoaded:boolean = false;

  invoiceData:string = "";
  invoiceDataList:any;

  userDataList:any;
  glAccountListData:any;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 10;

  unitFieldType:string = "vendorId";
  unitOrder:boolean = true;

  isMobile:boolean = false;

  accountUserDetails:any;

  selectedInput:string = "";
  columnField: any = {}

  columnData:any

  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;
  
  constructor(
    private userService:UserService,
  	private accountsService:AccountsService,
    private sessionService:SessionService,
    private router:Router
  ) { }



  getUserName(id){

  	var data = _.filter( this.userDataList, function( item ) {
      if(item.userId === id)
        return item;
    });

    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      return data[0].firstName+' '+data[0].lastName;
    }

  }

  onGlSearchFilter(){
    
    if(this.invoiceData != ""){

      let filtergroup = new jqx.filter();
      let filter_or_operator = 1;
      let filtervalue = this.invoiceData;
      let filtercondition = 'contains';
      let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
      filtergroup.operator = 'or';
      filtergroup.addfilter(filter_or_operator, filterData);

      this.datagrid.showfiltercolumnbackground(false);

      this.columnData.forEach(item => {
        if(item.datafield != 'Actions') {
          this.datagrid.addfilter(item.datafield, filtergroup, true);
        }
      })

      this.datagrid.applyfilters();
      
    }
    else {
      this.datagrid.clearfilters();
    }

  }

  @HostListener('window:onEditInvoice',['$event:detail'])
  onEditInvoice(detail){
    let dataRecord = this.datagrid.getrowdata(detail.rowId)
    let vendorId = dataRecord.vendorId
    this.router.navigateByUrl('/ams/expense/post-invoice/' + 1 + '/' + vendorId);
  }

  ngOnInit() {

    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }

    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }


    this.columnData = [{
         text: 'Vendor ID',
         datafield: 'vendorId',
         width: 100,
         pinned: true,
         cellsrenderer: cellsrenderer,
         renderer: columnrenderer
     }, {
         text: 'Vendor Name',
         datafield: 'vendorName',
         cellsrenderer: cellsrenderer,
         minwidth: 80,
         renderer: columnrenderer
     }, {
         text: 'Due',
         datafield: 'due',
         cellsrenderer: cellsrenderer,
         minwidth: 170,
         renderer: columnrenderer
     }, {
         text: 'Actions',
         cellsalign: 'center', 
         align: 'center',
         width: 120,
         cellsrenderer: (row: number): string => {
           return '<div class="simple-actions"> <a href="javascript:void(0)" class="text-secondary" onClick="editInvoiceEvent('+ row +')"><i class="fa fa-pencil" aria-hidden="true"></i></a></div>'
         },
         renderer: columnrenderer
     }];

  	var accountParams = {
      apartmentId: this.sessionService.apartmentId,
      groupId: 3
    }

  	this.accountsService.getGlAccountsByGroupId(accountParams).subscribe((res:any) => {
      this.glAccountListData = res;
    });

    let invoiceParams = {
      apartmentId: this.sessionService.apartmentId
    }

  	this.accountsService.getExpenseTrackerInvoicesByApartmentId(invoiceParams).subscribe((res:any) => {


  		var invoiceDataList = res;
  		this.totalItems = invoiceDataList.length;

      this.invoiceDataList = new jqx.dataAdapter({
        localData: invoiceDataList,
        datatype: "array"
      });

  		if(this.totalItems>this.itemLimit){
			  this.ItemEndIndex = this.itemLimit;
  		}
  		else {
  			this.ItemEndIndex = this.totalItems;
      }

      this.isInvoiceDataLoaded = true;
      
      let dataParams = {
        apartmentId: this.sessionService.apartmentId
      }

      this.userService.getAllUsersByApartmentId(dataParams).subscribe((res:any) => {
        this.userDataList = res;
      }, error => {
        
      });

  	})

  }


}

function editInvoiceEvent(row) {
  var event = new CustomEvent('onEditInvoice', {
    detail: {
      rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).editInvoiceEvent = editInvoiceEvent;
